button {
    cursor: pointer;
}

.home {
    height: fit-content;
}

.home__cover {
    width: 100%;
    height: calc(100vh - 6.25rem);

    background-image: url('../assets/HarieLow.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.home__hero-textarea {
    padding: 2rem;
    width: 100%;
    height: 100%;
    text-align: center;
    color: #fff;
    background: linear-gradient(rgba(0, 0, 0, 0.15), rgba(0, 0, 0, 0.75));

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.home__hero-info {
    font-size: 1rem;
    font-weight: 600;
    letter-spacing: 0.1rem;
}

.home__hero-title {
    font-size: 4rem;
    font-weight: 700;
    letter-spacing: 0.5rem;
    line-height: 1.3;

    margin: 3rem 0;
}

.home__hero-description {
    font-size: 1.25rem;
    line-height: 1.5;
    font-weight: 400;
}

.home__hero-button {
    margin-top: 3rem;
    padding: 1rem 0rem;
    font-size: 0.8rem;
    font-weight: 600;
    letter-spacing: 0.1rem;

    color: #333;
    background-color: #fff;

    transition: all 300ms;
}

.home__hero-button:hover {
    background-color: transparent;
    box-shadow: 0 0 0 2px #fff;
}

.home__pros {
    color: #333;
    background-color: #eee;
    padding: 5rem 1.5rem;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.home__pros-title {
    font-size: 1rem;
    font-weight: 600;
    letter-spacing: 0.1rem;
}

.home__pros > h2 {
    font-size: 1.25rem;
    font-weight: 700;
    margin-top: 2.5rem;
}

.home__pros > p {
    margin-top: 1rem;
    font-size: 0.8rem;
    line-height: 1.5;
}

.Link__order {
    text-decoration: none;
    color: #333;
    padding: 1rem 1.5rem;
    transition: all 300ms;
}

.Link__order:hover {
    color: #fff !important;
    letter-spacing: 0.25rem;
}
