.header {
    width: 100%;
    height: 4.5rem;
    /* background: yellow; */

    position: sticky;
    top: 0;
}

.header__container {
    width: 100%;
    height: 4.5rem;
    padding: 1rem 1.5rem;
    background-color: #fff;
    border-bottom: 1px solid #eee;

    display: flex;
    justify-content: center;
    align-items: center;
}

.logo {
    height: 1.25rem;
    cursor: pointer;
}

.header__icon-burger {
    width: 1.75rem !important;
    height: 1.75rem !important;
    color: #333;
}

button {
    margin: 0;
    padding: 0;
    background-color: transparent;
    border: none;
    cursor: pointer;
}

.burger__button {
    text-align: left;
    width: 2.25rem;
    height: 2rem;
    margin-right: auto;
    /* background-color: yellow; */
}

.cart__button {
    margin-top: -0.3rem;
    margin-left: auto;
    display: flex;
    align-items: center;
}

.cart__button > .Link__cart > span {
    font-size: 0.75rem;
    font-weight: 500;
    position: absolute;
    top: 0;
    right: 0;
    color: #fff;
    background-color: #aaa;
    border: 1.5px solid #fff;
    padding: 0.1rem 0.3rem;
    border-radius: 1rem;
}

.Link__cart {
    width: 2.25rem;
    height: 2rem;
    text-decoration: none;
    color: #333;
    /* background-color: yellow; */
    position: relative;
}

.Link__logo {
    height: 2rem;
    /* background-color: yellow; */
    display: flex;
    align-items: center;
}

.header__icon-cart {
    width: 1.5rem !important;
    height: 1.5rem !important;
    color: #333;
    position: absolute;
    bottom: 0;
    left: 0;
}
