.footer {
    width: 100%;
    height: 15rem;
    background-color: #333;
    color: #fff;
}

.footer__container {
    padding: 2rem;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    text-align: center;
}

.footer__copyrights {
    font-size: 0.75rem;
    line-height: 1.5;
}

.footer__follow {
    margin-top: 1rem;
    font-size: 1.25rem;
    font-weight: 700;
    line-height: 1.3;
}

button {
    cursor: pointer;
}

.footer__instagram {
    width: 1.5rem;
    height: 1.5rem;
}
